@import url(https://fonts.googleapis.com/css2?family=Hammersmith+One&display=swap);
@import url(https://fonts.googleapis.com/css2?family=Baloo+Tammudu+2:wght@700&display=swap);
body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

html,
body {
    width: 100%;
    height: 100%;
}

.footer {
    display: flex;
    align-items: center;
    justify-content: center;
    font-size: large;
    bottom: 0;
    left: 0;
    right: 0;
    width: 100%;
    height: 50px;
    color: black;
    text-align: center;
    border-top: 1px solid black;
}

.centerForm {
    display: flex;
    justify-content: center;
    align-items: center;
    width: 100%;
    height: 80vh;
    margin-top: 130px;
}

.contain {
    display: flex;
    position: fixed;
    width: 100%;
    justify-content: center;
    height: 106px;
    top: 0;
    left: 0;
    right: 0;
    background-color: #fff;
    z-index: 3;
}

.img:hover {
    color: rgb(147, 147, 147);
    transition: 0.5s;
}

.menu {
    width: 85%;
    display: flex;
    justify-content: space-between;
    align-items: flex-end;
    margin: auto;
}

.menu a:hover {
    color: rgb(147, 147, 147);
    transition: 0.5s;
}

.img {
    position: absolute;
    top: 30px;
    left: 30px;
    width: 50px;
    height: 50px;
}

.img span,
.img svg {
    width: 100%;
    height: 100%;
}

.menu li {
    list-style: none;
    margin-right: 15px;
    font-size: 25px;
    font-family: 'Hammersmith One', sans-serif;
    /* font-weight: 100; */
}

.menuBar {
    display: flex;
}

li svg {
    width: 24px;
    height: 24px;
}

a {
    color: black;
    text-decoration: none;
}

.menuBar p {
    position: absolute;
    font-size: 14px;
    font-family: 'Baloo Tammudu 2', cursive;
}

.menuBar a:hover + .p1 {
    transition: 2s;
    opacity: 1;
}

.menuBar a:hover + .p2 {
    transition: 2s;
    opacity: 1;
}

.p1 {
    right: 9.7vw;
    top: 3vw;
    opacity: 0;
}

.p2 {
    right: 7vw;
    top: 3vw;
    opacity: 0;
}

.infoContain {
    margin-top: 130px;
    width: 100%;
}

.columnContain {
    display: flex;
    flex-direction: column;
}

.columnContain hr {
    width: 80vw;
    border: 0;
    height: 2px;
    background-color: black;
    margin: 50px auto 50px auto;
}

.centerContain {
    display: flex;
    width: 80vw;
    margin: 0 auto 0 auto;
}

.infoMySelf {
    height: 215px;
}

.infoMySelf img {
    width: 20%;
    margin-right: 50px;
}

.infoMySelf h2 {
    font-size: 35px;
    line-height: 1.5;
}

.portfolio {
    flex-direction: column;
}

.pfForm {
    display: flex;
    justify-content: space-evenly;
}

.pfForm img {
    width: 50%;
}

.pfForm div {
    width: 40%;
    padding: 10px;
    background-color: black;
}

.tag,
.body {
    color: #fff;
    display: inline;
}

.tag {
    color: rgb(29, 138, 196);
}

.body {
    margin-left: 4px;
}

.postCenter {
    margin-top: 130px;
    width: 100%;
}

.postForm {
    width: 80vw;
    margin: 0 auto 0 auto;
    display: flex;
    flex-direction: column;
}

.thumbnail {
    width: 100%;
    color: black;
    display: flex;
    flex-wrap: wrap;
}
/* 
.postThum {
    width: 75%;
    border: 1px solid black;
    border-radius: 20px;
} */

.postThum {
    width: calc((100% - 10rem) / 5);
    height: 20rem;
    background-color: white;
    border-radius: 4px;
    box-shadow: rgb(0 0 0 / 4%) 0px 4px 16px 0px;
    transition: 0.5s all;
    margin: 1rem;
    overflow: hidden;
    display: flex;
    flex-direction: column;
}

.postThum:hover {
    transform: translateY(-10px);
    box-shadow: rgb(0 0 0 / 8%) 0px 12px 20px 0px;
}

.codeThum {
    width: 100%;
    height: 60%;
    /* background-color: yellow; */
}

.codeThum img {
    border-radius: 10px;
    width: 100%;
    height: 100%;
    object-fit: cover;
}

.postInfo {
    width: 100%;
    height: 40%;
}

.postInfo h4 {
    margin: 1rem 0;
}

.postInfo div {
    font-size: 0.875rem;
    height: 40%;
    margin: 0;
    text-overflow: ellipsis;
}

.postInfo div h1 {
    font-size: 0.875rem;
    margin: 0;
}

.postInfo span {
    font-size: 0.75rem;
    margin-bottom: 0;
}

.newpost {
    display: flex;
    width: 80vw;
    flex-direction: column;
    margin: 130px auto 0 auto;
}

.title {
    text-align: center;
}

.bodyContent {
    width: 50%;
    margin: 0 auto;
}

.bodyContent img {
    width: 100%;
}

